<template>
    <div style="background-color: #379EDE">
        <p-header>注册</p-header>
        <div class="login">
            <!--<div class="phone">-->
                <!--<van-cell-group>-->
                    <!--<van-field-->
                            <!--v-model="phone"-->
                            <!--type="number"-->
                            <!--required-->
                            <!--center-->
                            <!--clearable-->
                            <!--label="手机号"-->
                            <!--placeholder="请输入手机号"-->
                    <!--&gt;-->
                        <!--<van-button slot="button" size="small" type="primary">发送验证码</van-button>-->
                    <!--</van-field>-->
                <!--</van-cell-group>-->
            <!--</div>-->
            <div class="phone">
                <van-cell-group>
                    <van-field
                            clearable
                            label="用户名"
                            v-model="form.unickname"
                            placeholder="请输入用户名"
                            required
                    >
                    </van-field>
                </van-cell-group>

            </div>
            <div style="height: 0.2rem;"></div>
            <div class="phone">
                <van-cell-group>
                    <van-field
                            clearable
                            label="密码"
                            v-model="form.upassword"
                            type="password"
                            placeholder="请输入密码"
                            required
                    >
                    </van-field>
                </van-cell-group>
            </div>
            <div style="height: 0.2rem;"></div>
            <div class="phone">
                <van-cell-group>
                    <van-field
                            clearable  v-model="form.utel"
                            label="手机号"
                            placeholder="请输入手机号"
                            required
                    >
                    </van-field>
                </van-cell-group>
            </div>
            <!--<div class="code">-->
                <!--<van-field-->
                        <!--v-model="sms"-->
                        <!--label="验证码"-->
                        <!--type="password"-->
                        <!--placeholder="请输入验证码"-->
                        <!--required-->
                <!--/>-->
            <!--</div>-->
            <div style="height: 0.2rem;"></div>
            <div class="btn">
                <div class="user-login" @click="register">注册</div>
            </div>
            <div class="login-type1">
                <span class="toggle-type">初次注册成功将自动登录</span>
            </div>
            <div class="login-type">
                <router-link :to="{name:'login'}">
                <span class="toggle-type">返回登录</span>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import PHeader from '../../components/p-header/p-header';
    import {register} from '../../api/users';
    import {SUCCESS} from "../../config/base";
    import Vue from 'vue';
    import { Toast } from 'vant';

    Vue.use(Toast);
    export default {
        name: "Register",
        data() {
            return {
                form:{
                    unickname: '',
                    upassword: '',
                    utel:''
                }
            }
        },
        components: {
            PHeader
        },
        methods:{
            register(){
                let formdata=this.form;
                if (!formdata.unickname){
                    Toast('用户名不能为空');
                    return;
                }
                if (!formdata.upassword){
                    Toast('密码不能为空');
                    return;
                }
                if (!formdata.utel){
                    Toast('手机号不能为空');
                    return;
                }
                register('/api/user',this.form)
                    .then(res=>{
                        let data=res.data;
                        if (data.code===SUCCESS){
                            Toast(data.msg);
                            setTimeout(()=>{
                                this.$router.push({name:'index'});
                            },500);

                        }else{
                            Toast(data.msg);
                        }
                    })
            }
        }
    }
</script>

<style scoped>
    .box {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
    }

    .login {
        width: 100%;
        height: 4rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -100%);
        padding: 0 0.24rem;
        text-align: center;
    }

    .phone {
        height: .9rem;
        box-sizing: border-box;
        padding-bottom: 0;
        align-items: center;
    }

    .code {
        height: .7rem;
        box-sizing: border-box;
        padding-bottom: 0;
        align-items: center;
    }

    .btn {
        margin-top: 2em;
        color: #222222;
        opacity: 0.3;
        border-radius: .2rem;
        height: .9rem;
        line-height: .9rem;
        background-color: #FE8C00;
        background-image: linear-gradient(-133deg, #FFBD00 0%, #FFD000 100%);
    }

    .login-type {
        font-size: .28rem;
        color: #222;
        text-align: center;
    }
    .login-type1 {
        font-size: .28rem;
        color: #222;
        text-align: center;
    }
    .toggle-type {
        padding: .3rem .1rem .1rem .1rem;
        display: inline-block;
        cursor: pointer;
        color: black;
    }
</style>